.Modal{
    position: fixed;
    left: 15%;
    top: 20%;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 3px 3px 10px 0 #aaa;
    box-sizing: border-box;
    animation: detailBox 1s ease-out;
}

@media (min-width: 11000px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}

@media(max-width: 700px){
    .Modal{
        top: 10%;
    }
}

@keyframes detailBox {
    0%{
        opacity: 0;
        transform: translateY(-100%);
    }

    100%{
        opacity: 1;
        transform: translateY(0px);
    }
}