.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #000;
    color: #eee;
    padding: 10px;
    box-shadow: 0 3px 6px 0 #555;
    font-size: 25px;
    font-weight: bold;
}

.title{
    width: 13%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.MovieImg{
    height: 3rem;
    width: 3rem;
}

.titleName{
    padding-left: 10px;
}

.searchBox{
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 6px;
    margin-left: 20px;;
    width: 50%;
    padding-right: 20px;
    background-color: white;
}

input{
    color: black;
    font-size: 16px;
    font-weight: bold;
    width: 100%;
    margin-left: 15px;
    border: none;
    outline: none;
}